  right: -8px;
}
.journey_mobile__slide_right {
  left: -1px;
  align-self: flex-end;
  padding-left: 20px;
  border-left-width: 1px;
}
.journey_mobile__slide_right::before {
  left: -8px;
}
.journey_mobile__slide_first::before {
